				:key="index"
				:span="6"
				:xs="12"
			>
			<div :class="{'colover':item.hover,'col':'true','col_phone':$store.state.w_win<=850}"
				@mouseover="(e)=>{mouseover(e,item)}"
				@click="(e)=>{clickitem(e,item)}"
				@mouseout="spanisn()"
			>
				<div :class="{'imgboxover':item.hover,'imgbox':'true','imgbox_phone':$store.state.w_win<=850}">
					<img class="img" :src="item.imgsrc" alt="">
				</div>
				<div :class="{'texthover':item.hover,'text':'true','text_phone':$store.state.w_win<=850}">
					<el-tooltip :content="item.title" placement="bottom" effect="light">
						<h4>{{item.title | ellipsistitle}}</h4>
					</el-tooltip>
					<div class="con">{{item.contxt | ellipsis}}</div>
				</div>
			</div>
			</el-col>
