<script>
export default {
	components:{
	},
	name: 'proitem',
	filters: {
	ellipsistitle(value){
		if (value.length > 11) {
			return value.slice(0,11) + '...'
			}
		return value
	},
	ellipsis(value) {
		if (!value) return ''
		if (value.length > 14) {
			return value.slice(0,14) + '...'
			}
		return value
		}
	},
	props: {
		productarr:Array,
		brench:Number,
		sysitem:Object
  },
	mounted:function(){
		
		
		// console.log(this.productarr)
	},

  methods:{
	clickitem(e,item){
		this.pro_id({id:item.id,brench:this.brench})
	},
	mouseover(e,item){
		this.spanisn();
		item.hover = true;
		// console.log(e)
	},
	spanisn(){
			this.productarr.forEach((item)=>{
				item.hover = false
			})
		}
	
},
  data() {
	return {
		
	}
  },

computed: {
	
},

watch:{

}
}
