<template>
  <div class="product_fa">
   <!-- 六大系统 -->
   <Prosysitem v-if="productsys" :productarr="productsys" />
   <!-- 单页系统 -->
   <Proitem  v-if="productlist" :sysitem="sysitem" :productarr="productpage" :brench="brench" />
   <el-pagination
      v-if="productlist" 
      background
      @current-change="productarrchange"
      layout="prev, pager, next"
      :page-size="8"
      :total="productlist.length">
    </el-pagination>
  </div>
</template>
<script>
import prosysitem from "@/components/views/page/prosysitem.vue"
import proitem from "@/components/views/product/proitem.vue"
  
export default {
  name: 'product',
  props: {
    
  },
  components:{
    Prosysitem:prosysitem,
    Proitem:proitem
  },
  mounted(){
    // console.log(this.$route.query.brench)
    if(this.$route.query.id>=0 && this.$route.query.brench>=0){
      // console.log(this.$route.query.id);
      this.pro_id({id:this.$route.query.id,brench:this.$route.query.brench})
    }else if(this.$route.query.brench>=0){
      //显示单系统页面
      this.productsys =  false;
      this.brench = parseInt(this.$route.query.brench);
      this.sysitem = this.$store.state.prolist[this.brench]
      this.productlist = this.$store.state.productlist[this.brench];
      this.productpage = this.productlist.slice(0,8)
    }else{
      //显示六大系统页
     
      this.productsys =  this.$store.state.prolist;
      this.productlist =  false;
    }
    
  },
  methods:{
    productarrchange(e){
      this.productpage = this.productlist.slice(8*(e-1),8*e);
    }
    },
  data() {
    return{
        productlist:[],
        productsys:[],
        productpage:[],
        brench:0,
        sysitem:{}
  }}

  }
</script>
<style>
.product_fa{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /*padding: 3rem*/
}
</style>
