import Vue from 'vue'
import App from './App.vue'
import Vuex from 'vuex';
import axios from 'axios';
import Router from 'vue-router';
import VueAxios from 'vue-axios';
// import Vuex from 'vuex';
import iView from 'element-ui';
import {
	routes
} from './routers/router.js';
import store from './store/index.js';
// import './App.css'
// import './iview.css'
// import 'iview/dist/styles/iview.css';
import 'element-ui/lib/theme-chalk/index.css'
import './mainstyle.scss'
import 'element-ui/lib/theme-chalk/display.css';


Vue.use(Vuex).use(Router).use(VueAxios, axios);
Vue.use(iView);


const router = new Router({
	mode: 'hash',
	routes
})



new Vue({
	render: h => h(App),
	router: router,
	store
}).$mount('#app')


//进入产品详情页,直接传id
Vue.prototype.pro_id = (params) => {
	document.body.scrollTop = document.documentElement.scrollTop = 0
	router.push({
		path: `/product/id?id=${params.id}&brench=${params.brench}`
	});
};


//本页刷新product页、1.上下翻页时用
Vue.prototype.benye = (params) => {
	document.body.scrollTop = document.documentElement.scrollTop = 0
	router.push({
		path: `/product?id=${params.id}&brench=${params.brench}`
		// params: {
		// 	'id': param.id
		// }
	});
};

//进入主分类
Vue.prototype.pro_brench = (params) => {
	document.body.scrollTop = document.documentElement.scrollTop = 0
	// console.log(params.brench)
	router.push({
		path: `/product/brench?brench=${params.brench}`
	});
};