<template>
  <div id="allmap" :class="{'allmap':'true','allmap_phone':$store.state.w_win<=850}"></div>
</template>
<script>
import BMap from 'BMap'

export default {
  name: 'adtress',
  props: {
    
  },
  components:{
  },
methods: {
    createMap () {
      /* eslint-disable */
      // 创建Map实例
      var map = new BMap.Map("allmap")
      // 初始化地图,设置中心点坐标和地图级别
      map.centerAndZoom(new BMap.Point(124.325924,40.075798), 18)
      //添加地图类型控件
      map.addControl(new BMap.MapTypeControl({
        mapTypes:[BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
      }))
      // 设置地图显示的城市 此项是必须设置的
      map.setCurrentCity("丹东")
      //开启鼠标滚轮缩放
      map.enableScrollWheelZoom(true)
      /* eslint-enable */
      var point = new BMap.Point(124.325924,40.075798);   
      var marker = new BMap.Marker(point);        // 创建标注   
      map.addOverlay(marker);                     // 将标注添加到地图中
      var label = new BMap.Label('宇源消防', {       // 创建文本标注
      position: point,                          // 设置标注的地理位置
      // offset: new BMap.Size(10, 20)           // 设置标注的偏移量
        })
      label.setStyle({                              // 设置label的样式
      color: '#00f',
      fontSize: '1rem',
      border: '0',
      backgroundColor: '#ff0',
      padding:'2px'
        })  
      map.addOverlay(label);                        // 将标注添加到地图中
    }
  },
  mounted () {
    this.createMap()
  }


}
</script>
<style>
.allmap{
  height:20rem;
  width: 40rem;
  border:1px solid #ccc;
}
.allmap_phone{
  width: 90vw;
  height:45vw;
}
</style>
