  <div id="app">
	<el-backtop></el-backtop>
	<Header :class="{header:'true','header_phone':this.$store.state.w_win<=850}" />
    <router-view></router-view>
    <el-footer id="footer">
		<Footer />
	</el-footer>
  </div>
