<script>
import 'element-ui/lib/theme-chalk/display.css';
import store from './store/index.js';
// import Vue from 'vue';
// Vue.prototype.GLOBAL = global_;

//import home from './components/views/home/home.vue';
//等比缩放字体大小
var w_win = document.documentElement.clientWidth || document.body.clientWidth;

var _html = document.getElementsByTagName("html")[0];
store.dispatch("setvalue_win",w_win);
if(w_win<=750){
	_html.style.fontSize = w_win/18 +'px';
	// _html.style.fontSize = '50px';
}else{
	_html.style.fontSize = "16px";
}







import header from "@/components/temp/header.vue"
import footer from "@/components/temp/footer.vue"
export default {

  name: 'App',
  components: {
    Header:header,
    Footer:footer
  },
	mounted () {
		
		//获取屏幕宽高度,动态改变文字大小
		var that = this;
		window.onresize = () => {
			return (() => {
				// window.clientHeight = document.body.clientHeight;
				w_win = document.body.clientWidth;
				that.$store.dispatch("setvalue_win",w_win);
				//设置根元素字体大小
				if(w_win<=750){
					_html.style.fontSize = w_win/18 +'px';
				}else{
					_html.style.fontSize ='16px';
				}
				})()
			}
		

  },
  methods:{	
	},
  data(){
	return{
		HEIGHT:Number
	}
  }
}
</script>

