<template>
  <div class="pro_fa">
    <div :class="{'pro_head_fa':'true','pro_head_fa_phone':$store.state.w_win<=850}">
      <img class="head_img" :src="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].imgsrc" alt="">
      <div class="pro_head_txt">
        <h1>{{this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].title}}</h1>
        <div>{{this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].contxt}}</div>
      </div>
    </div>
    <Title title="产品详情" />
    <div id="pro_xiangqing">
      <div>产品名称：{{this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].title}}</div>
      <div>产品特点：</div>
      <ul>
        <li v-for="(item,index) in this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].xiangqing"
        :key="index"
        >{{item}}</li>
      </ul>
    </div>
    <Title v-show="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshuimg" title="图文参数" />
    <div v-show="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshuimg"
      :class="{'canshuimg':'true','canshuimg_phone':$store.state.w_win<=850}">
      <img v-for="(item,index) in this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshuimg" :key="index" :src="item" alt="">
    </div>
    <Title v-if="$store.state.w_win>850"  v-show="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshutable" title="技术指标" />
    <el-table
     v-if="$store.state.w_win>850" 
     id="el-table"
     border
     v-show="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshutable"
     :data="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshutable"
     stripe
     style="width: 100%">
     <el-table-column
      align="center"
      prop="title"
      :label="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshutableheader[0]"
      width="500">
     </el-table-column>
     <el-table-column
      prop="con"
      :label="this.$store.state.productlist[this.$route.query.brench][this.$route.query.id].canshutableheader[1]">
     </el-table-column>
  </el-table>
    <div v-if="$store.state.w_win>850" class="guibutton_fa">
      <div @click="pro_back()">
        <i class="el-icon-back"></i>
        上一个 {{backtxt | ellipsis}}</div>
      <div @click="pro_next()">
        下一个 {{nexttxt | ellipsis}}
        <i class="el-icon-right"></i></div>
    </div>
  </div>
</template>
<script>
import title from "@/components/views/page/title.vue"

export default {
  name: 'productz',
  props: {
    
  },
  components:{
    Title:title
  },
  filters: {
    ellipsis(value) {
      if (!value) return '没有了'
      if (value.length > 12) {
        return value.slice(0,12) + '...'
        }
      return value
      }
  },
  mounted(){
    this.proid = this.$route.query.id;
    if(this.proidb >= 0){
      if(this.$store.state.w_win<=850){
          this.backtxt="产品"
      }else{
       this.backtxt = this.$store.state.productlist[this.$route.query.brench][this.proidb].title
      }
    }
    if(this.proidn<this.$store.state.productlist[this.$route.query.brench].length){
      if(this.$store.state.w_win<=850){
          this.nexttxt="产品"
      }else{
       this.nexttxt = this.$store.state.productlist[this.$route.query.brench][this.proidn].title
    }
  }},
  methods:{
    pro_back(){
       this.benye({id:this.proidb,brench:parseInt(this.$route.query.brench)});
    },
    pro_next(){
      if(this.proidn<this.$store.state.productlist[this.$route.query.brench].length){
        this.benye({id:this.proidn,brench:parseInt(this.$route.query.brench)})
      }else{
        this.benye({id:-1,brench:parseInt(this.$route.query.brench)})
      }
      }
    },
  data() {
    return{
      proid:0,
      brench:0,
      backtxt:"",
      nexttxt:""
      // productitems:[]
  }},
  computed:{
    proidb(){return parseInt(this.$route.query.id)-1},
    proidn(){return parseInt(this.$route.query.id)+1}
  }

}
</script>
<style scoped>
