    height: 60vw;
    margin:0;
    margin-top: 2.8rem;
    
    /*width: 100%;*/

  }
  .pro_head_txt{
      display: flex;
      flex-direction: column;
      justify-content: center;
  }
  .pro_head_txt h1{
    color:blue;
    border-bottom: 1px solid #ccc;
  }
   .pro_head_txt div{
    /*margin-top: 2rem;*/
    line-height: 1.8rem
   }

  .pro_head_fa_phone h1{
    font-size:1rem;
    margin: 0;
    padding: 0;
  }





   #pro_xiangqing{
