   }
   #pro_xiangqing li{
     font-size:0.9rem;
     line-height: 1.6rem
   }
   .canshuimg{
    display: flex;
    flex-direction: column;
    align-items: center
   }
  .canshuimg img{
    width: 60vw;
  }
  .canshuimg_phone img{
    width: 100vw;
  }
   .guibutton_fa{
    /*width: 50rem ;*/
    display: flex;
