<template>
<div>
<el-row v-if="this.$store.state.w_win>850" class="headerbox">
  <el-col :span="4">
	<div class="logo">
		<img class="logoimg" src="./yyxf.png" alt="">
	</div>
  </el-col>
  <el-col v-for="(item) in items"
	:key="item.id" :span="item.span"
	@mouseover.native="(e)=>{onmouseover(e,item)}"
	@mousedown.native="(e)=>{onclick(e,item)}"
	@mouseup.native="(e)=>{onclickup(e,item)}"
	:class="{'col_box':'true','active':item.span==7}"
	:style="item.active?{background:'rgba(0,0,255,0.7)',color:'#fff'}:{}">
		<div><i v-show="item.span==7" :class="item.icon"></i> {{item.label}}</div>
  </el-col>
  <transition name="el-zoom-in-top">
	<div v-show="items[1].span==7 && productbox"
		class="productbox"
		@mouseout="proonmouseout"
		>
		<Prolist @pro_brench_click="onclickup()"/>
	</div>
  </transition>
</el-row>
<div v-if="this.$store.state.w_win<=850" class="phone_header">
	<div class="phone_headerlogo">
		<img src="./yyxf_phone.png" alt="">
		<el-divider direction="vertical"></el-divider>
		宇源消防
	</div>
	<i @mousedown="mousedown_i"
       @mouseup="mouseup_i"
		:class="{'el-icon-s-fold':!tableshow,'el-icon-s-unfold':tableshow,'itwo':'true','itwoclickdown':clickdown}"></i>
		<transition name="el-zoom-in-center">
			<div class="caidan" v-show="tableshow">
				<div :class="{'caidan_item':'true','caidan_item_last':item.id==5}"  v-for="(item) in items"
					:key="item.id"
					@click="(e)=>{onclick(e,item)}"
					>
					<i :class="item.icon"></i>
					{{item.label}}
				</div>
			</div>
		</transition>
</div>
</div>
</template>
<script>
import prolist from "@/components/temp/prolist.vue"
export default {
	components:{
		Prolist:prolist
	},
	name: 'headerbox',
	props: {
		
	},
	mounted(){
		// console.log(this.$route.query)
	},
	methods:{
		mousedown_i(){
			this.clickdown=true
		},
		mouseup_i(){
			this.clickdown=false;
			this.tableshow=!this.tableshow
		},
		proonmouseover(){
			this.productbox = true;
		},
		proonmouseout(e){
			if(e.clientY>350){
				this.productbox = false;
			}
		},
		onmouseover(e,item){
			// console.log(e)
			this.spanisn()
			item.span = 7;
			if(item.id==2){
				this.productbox = true;
			}

		},
		spanisn(){
			// console.log("1111");
			this.items.forEach((item)=>{
				item.span = 3;
				item.active = false
			})
		},
		onclick(e,item){
			document.body.scrollTop = document.documentElement.scrollTop = 0;
			this.tableshow = false;
			this.spanisn();
			item.span = 7;
			item.active = true;
			if(this.$route.query.id){
				this.$router.push({path:item.path});
			}else if(this.$route.path == item.path){
				return
			}else{
				this.$router.push({path:item.path});
			}
			
			// console.log(item);
		},
		onclickup(e,item){
			if(item){
				this.spanisn();
				item.span = 7;
			}
			// console.log(111111)
			this.productbox=false
		}
		
		},
	data() {
	return {
		tableshow:false,
		clickdown:false,
		items:[{id:1,label:"网站首页",span:7,icon:"el-icon-eleme",path:"/main"},
			{id:2,label:"产品中心",span:3,icon:"el-icon-s-cooperation",path:"/product/brench?brench=-1"},
			{id:3,label:"案例工程",span:3,icon:"el-icon-paperclip",path:"/gongchenganli"},
			{id:4,label:"资料下载",span:3,icon:"el-icon-download",path:"/xitongwansan"},
			{id:5,label:"关于我们",span:3,icon:"el-icon-phone-outline",path:"/connenctus"}
			],
			productbox:false
    }
  },
  computed: {
	path(){
		return this.$route.path
		}
},
  watch:{
	path(newvalue){
		if(newvalue.indexOf("product") != -1){
			this.onclickup("e",this.items[1])
		}
	}
  }
  
}
</script>
<style>
.headerbox{
	padding-top:6rem;

}
.col_box{
	user-select:none;
	background:rgba(135,206,235,0.7);
	margin: 2px;
	transition: width 0.5s,background 0.5s;
}
.col_box:nth-of-type(2){
	border-radius: 1rem 0 0 0;
	/*border:2px solid red;*/
}
.col_box:nth-of-type(6){
	border-radius: 0 0 1rem 0;
}
.active,.col_box:hover{
	background:rgba(135,206,235,0.7);
	color: 	#00008B
}
.logobox{
	font-size: 0.5rem;
	border: 1px solid red;
}

.productbox{
	height: 200px;
	/*margin: -1.5%;*/
	margin-top: 4rem;
	position: fixed;
	width: 100%;
	/*width: 120%;*/
	background-color:rgba(256,256,256,0.95);
}
.phone_header{
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background:#444;
	width: 100%;
	top:0;
	font-size:1.4rem;
	line-height: 2.8rem;
	border-bottom:1px solid skyblue;
}
.phone_headerlogo{
	margin-left: 1rem;
	display: flex;
	align-items: center;
}
.phone_headerlogo img{
	height: 1.4rem;
}
.phone_header .ione{
	margin-left: 1rem
}
.phone_header .itwo{
	margin-right: 1rem
}
.phone_header .itwoclickdown{
	color:#ccc;
}
.caidan{
	position: absolute;
	top: 2.8rem;
	right: -8rem;
	color: #fff;
	background:#444;
	width:16rem;
	height: 14rem;
	/*border-top:1px solid #ccc;*/
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.caidan_item{
	margin-left: 1rem;
	padding-left: 0.5rem;
	border-bottom: 1px solid #ccc;
}
.caidan_item_last{
	border:0;
}
.logo{
	/*border:1px solid red;*/
	height:5.6rem;
	margin-top: -2rem;
}
.logoimg{
	width: 70%;
	height: 100%;
}
</style>
